<template>
  <div class="pl-1 pr-1 mb-4">
    <label color="primary">Produk Terbaru</label>
    <v-row dense>
      <v-col v-for="item, i in items" :key="i" cols="6" md="2" xs="6">
        <v-card link @click="toDetail(item)">
          <v-img :src="item.foto[0]"></v-img>
          <v-card-subtitle class="text-truncate">{{ item.name }}</v-card-subtitle>
          <v-card-text>
            <v-row
              align="center"
              class="mx-0"
            >
            <div class="text-subtitle-1 font-normal">
              <span class="text-red">Rp<span class="text-harga">{{ currencyRp(item.harga) }}</span></span>
              </div>
            </v-row>
            <v-row>
                <v-rating
                  :value="item.bintang"
                  color="amber"
                  dense
                  half-increments
                  readonly
                  size="12"
                ></v-rating>
                <div class="grey--text ms-3">
                  {{ item.terjual }} Terjual
                </div>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
export default {
  name: 'ListProduct',
  data () {
    return {
      model: this.value
    }
  },
  props: {
    label: {
      type: String,
      required: false,
      default: 'Search'
    },
    items: {
      type: Array,
      required: false
    },
    appendIcon: {
      type: String,
      required: false,
      default: 'mdi-shopping-search-outline'
    },
    value: {
      required: false
    }
  },
  methods: {
    onChange () {
      this.$emit('input', this.model)
    },
    currencyRp (nominal) {
      return nominal.toFixed(0).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, '$1,')
    },
    toDetail (item) {
      this.$emit('toDetail', item)
    }
  }
}
</script>
<style>
.someDiv {
    line-height: 1.5em;
    height: 10em;       /* height is 2x line-height, so two lines will display */
    overflow: hidden;  /* prevents extra lines from being visible */
}
.font-normal{
  font-weight: 400;
  text-overflow: ellipsis;
}
.text-red{
  color: red !important;
  font-size: .75rem !important;
}

span.text-harga{
  font-size: 1rem !important;
}
</style>
