<template>
  <div>
    <v-card color="transparent" flat id="Home">
      <v-img width="100%" height="100%" style="margin-top: -17vmax;" src="~@/assets/theme/bg/Frame.png">
        <!-- <v-card-title>Satu-satunya platform HR dengan semua yang Anda butuhkan, untuk semua orang!</v-card-title> -->
        <v-container class="section home pa-0" style="margin-top: 9vmax;">
          <!-- <FreetextOutlined label="Ketik barang impianmu" /> -->
          <Carousel />
          <!-- <Kategori :items="categories" /> -->
          <Product :items="products"
          v-on:toDetail="toDetail"/>

        </v-container>
      </v-img>
    </v-card>
    <!-- <v-container>
    </v-container> -->
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Carousel from '../carousels/Carousel.vue'
// import FreetextOutlined from '@/components/freetext/Outlined.vue'
// import Kategori from '@/components/lists/Kategori.vue'
import Product from '@/components/produk/Index.vue'
export default {
  name: 'IndexPagePublic',
  components: { Carousel, Product }, //  Kategori,
  data: () => ({
    isMobile: false
  }),
  mounted () {
    // store.dispatch('produk/all')
    this.isMobile = window.innerWidth < 868
    window.addEventListener('resize', this.checkIsMobile)
  },
  computed: {
    ...mapState('home', {
      categories: (state) => state.categories
    }),
    ...mapState('produk', {
      products: (state) => state.produks
    }),
    imaage () {
      return require('@/assets/theme/mobile/HROneMockup1.png')
    },
    general () {
      return require('@/assets/theme/general.png')
    },
    penggajian () {
      return require('@/assets/theme/penggajian.png')
    }
  },
  methods: {
    ...mapActions('produk', []),
    checkIsMobile () {
      this.isMobile = window.innerWidth < 868
    },
    toDetail (item) {
      store.dispatch('produk/toDetail', item)
      this.$router.push('/detail')
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.checkIsMobile)
  }
}
</script>
