<script>
import { mapState } from 'vuex'
export default {
  name: 'CarouselComponent',
  data: () => ({
    urutan: 0,
    cycle: false
  }),
  mounted () {
    this.runs()
  },
  computed: {
    ...mapState('home', {
      state: (state) => state
    })
  },
  methods: {
    runs () {
      setInterval(() => {
        if (this.urutan >= this.state.banner.length) {
          this.urutan = 0
        } else {
          this.urutan = this.urutan + 1
        }
      }, 4000)
    }
  }
}
</script>
